import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: r => require.ensure([], () => r(require('@/pages/Home')), 'Home'),
      meta: {
        title: '万科金域华府'
      }
    },
    {
      path: '/info',
      name: 'Info',
      component: r => require.ensure([], () => r(require('@/pages/Info')), 'Info'),
      meta: {
        title: '便民信息'
      }
    },
    { path: '*', redirect: '/'},
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next();
})

export default router;



